export default {
  data() {
    return {
      // 购买记录数据
      buyList: [],
      user_id: ""
    };
  },
  onLoad() {
    this.user_id = uni.getStorageSync("user_id");
    // 购买记录页面接口
    this.getBuy();
  },
  methods: {
    // 购买记录页面接口
    getBuy() {
      this.$http
        .get("/memberPackage/getBuy", { params: { user_id: this.user_id } })
        .then(res => {
          console.log(res);
          this.buyList = res.data;
        });
    }
  }
};
